* { box-sizing: border-box; }

.workspace:after {
  content: '';
  display: block;
  clear: both;
}

.workspace .work {
    margin: 5px 5px;
    float: left;
}

.workspace .work img, .workspace .work video {
    width: 100%;
    box-shadow: 0px 0px 4px rgba(0,0,0,0.1);
    transition: 0.5s;
}

.workspace .work img:hover, .workspace .work video:hover {
    transform: scale(1.04);
    box-shadow: 0px 2px 8px rgba(0,0,0,0.2);
}

.workspace .work .contents {
    padding: 0px;
}

.workspace .work .title {
    font-size: 17px;
    font-weight: 500;
}

.workspace .work .description {
    font-size: 13px;
    font-weight: 300;
    line-height: 15px;
    display: inline-block;
}

.workspace .work .date {
    color: var(--primary-color-darker);
    margin: 2px 5px;
    display: inline;
    word-break: keep-all;
    font-size: 14px;
    font-weight: 500;
}

.workspace .work .keyword .tag {
    border: 0.5px solid rgb(154, 154, 154);
    color: var(--text-color-2);
    font-size: 10px;
    font-weight: 100;
    display: inline-block;
    padding: 1px 3px;
    border-radius: 3px;
    margin-right: 3px;
}

.theme-dark .workspace .work .keyword .tag {
    font-weight: 300;
}